import { NavLink } from "react-router-dom";
import { Button, Drawer } from 'antd';
import { useState } from 'react';
import {
    MobileOutlined,
    MailOutlined,
    FacebookFilled,
    TwitterSquareFilled,
    InstagramFilled,
    UserOutlined,
    MenuOutlined,
    LinkedinFilled
} from '@ant-design/icons';

function AppHeader() {
    const [open, setOpen] = useState(false);
    const showDrawer = () => {
      setOpen(true);
    };
    const onClose = () => {
      setOpen(false);
    };
    return (
        <div className="container">
            {/* topbar */}
            <div className="topBar">
                <div className="contactInfo">
                    <ul>
                        <li>
                            <a href="tel:03451279940"><MobileOutlined /><span>+923451279940</span></a>
                        </li>
                        <li>
                            <a href="mailto:shahzoreasif2@gmail.com"><MailOutlined /><span>shahzoreasif2@gmail.com</span></a>
                        </li>
                    </ul>
                </div>
                <div className='otherInfo'>
                    <ul className='socialMedia'>
                        <li><a href='https://www.facebook.com' target="_blank"><FacebookFilled /></a></li>
                        <li><a href='https:www.instagram.com' target="_blank"><InstagramFilled /></a></li>
                        <li><a href='https:www.twitter.com' target="_blank"><TwitterSquareFilled /></a></li>
                        <li><a href='https://www.linkedin.com/in/muhammadshahzore' target="_blank"><LinkedinFilled /></a></li>
                    </ul>
                    <button><UserOutlined /> My Account</button>
                </div>
            </div>
            {/* header */}
            <div className='header separator'>
                <div className='logo'><NavLink to='/grocery/'>Grocery</NavLink></div>
                <div className="mobileVisible">
                <Button type="primary" onClick={showDrawer}>
                <MenuOutlined />
                </Button>
                <Drawer title="Menu" placement="right" onClose={onClose} open={open} >
                <nav>
                    <ul>
                        <li><NavLink to='/grocery/'>Home</NavLink></li>
                        <li><NavLink to='/grocery/about'>About</NavLink></li>
                        <li><NavLink to='/grocery/shop'>Shop</NavLink></li>
                        <li><NavLink to='/grocery/faq'>Faq</NavLink></li>
                        <li><NavLink to='/grocery/contact'>Contact</NavLink></li>
                    </ul>
                </nav>
                </Drawer>
                </div>
                <nav className="mobileHidden">
                    <ul>
                        <li><NavLink to='/grocery/'>Home</NavLink></li>
                        <li><NavLink to='/grocery/about'>About</NavLink></li>
                        <li><NavLink to='/grocery/shop'>Shop</NavLink></li>
                        <li><NavLink to='/grocery/faq'>Faq</NavLink></li>
                        <li><NavLink to='/grocery/contact'>Contact</NavLink></li>
                    </ul>
                </nav>
            </div>
        </div>
    );
}

export default AppHeader;